/* kelly-slab-regular */

@font-face {
  font-family: 'Kelly Slab';
  src: url('../fonts/kellyslab/kellyslab-regular.eot');
  src: url('../fonts/kellyslab/kellyslab-regular.eot?#iefix') format('embedded-opentype'), url('../fonts/kellyslab/kellyslab-regular.woff2') format('woff2'), url('../fonts/kellyslab/kellyslab-regular.woff') format('woff'), url('../fonts/kellyslab/kellyslab-regular.ttf') format('truetype'), url('../fonts/kellyslab/kellyslab-regular.svg#kelly_slabregular') format('svg');
  font-weight: normal;
  font-style: normal;
}


/* icomoon font for icons */

@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon/icomoon.eot?jte5vh');
  src: url('../fonts/icomoon/icomoon.eot?jte5vh#iefix') format('embedded-opentype'), url('../fonts/icomoon/icomoon.ttf?jte5vh') format('truetype'), url('../fonts/icomoon/icomoon.woff?jte5vh') format('woff'), url('../fonts/icomoon/icomoon.svg?jte5vh#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-bars:before {
  content: "\e900";
}

.icon-search:before {
  content: "\e901";
}

.icon-youtube:before {
  content: "\e902";
}

.icon-twitter:before {
  content: "\e903";
}

.icon-facebook:before {
  content: "\e904";
}

.icon-facebook-f:before {
  content: "\e904";
}