@import "mixins";
@import "fonts";
@import "vars";
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
}

@media (min-width: 992px) {
  html,
  body {
    height: 100%;
  }
}

a {
  text-decoration: none!important;
}

button:hover {
  cursor: pointer;
}

li {
  list-style: none;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: $font-primary;
  @include rfs(15);
}

.main {
  flex: 1 0 auto;
}

.container {
  @media (min-width: 320px) {
    max-width: 1240px;
  }
}

// social icons
.social-iconset {
  display: flex;
}

.social-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 21px;
  height: 21px;
  margin-right: 7px;
  margin-left: 7px;
  background-color: #fff;
  border-radius: 50%;
  transition: all .3s ease;
  .icon {
    color: #000000;
    font-size: 16px;
  }
  &:hover {
    background-color: lighten($yellow, 20);
  }
}

// navigation
.nav-item {
  margin-left: 12px;
  text-transform: uppercase;
  letter-spacing: 1.3px;
  transition: all .3s ease;
  a {
    color: #fff;
  }
  &:first-child {
    margin-left: 0;
  }
}

.nav-item.active a.nav-link {
  color: $yellow;
}

.nav-link {
  white-space: nowrap;
  font-size: 15px;
  &:hover {
    color: lighten($yellow, 20);
  }
}

// HEADER
.header {
  display: flex;
  align-items: center;
  min-height: 110px;
  background-color: #000;
  // logo
  .navbar-brand {
    max-width: 99px;
    min-width: 70px;
    margin-left: 12px;
    padding: 0;
    .header__logo {
      width: 100%;
      transition: transform .3s ease;
      &:hover {
        transform: scale(1.05);
      }
    }
  }
  .navbar {
    width: 100%;
    justify-content: space-between;
    color: #FFFFFF;
    // header menu
    .navbar-collapse {
      flex-basis: auto;
      @media only screen and (max-width: 992px) {
        flex-basis: 100%;
      }
    }
  }
  // header search
  .header__search {
    display: flex;
    margin-left: 19px;
  }
  .header__search-input {
    width: 0;
    visibility: hidden;
  }
  .navbar__search-btn {
    font-size: 20px;
    color: #fff;
    background-color: transparent;
  }
  // language switcher
  .header__lang {
    display: flex;
    align-items: center;
    margin-left: 11px;
    font-size: 10px;
    line-height: 1;
  }
  .header__lang-link {
    padding-left: 5px;
    padding-right: 5px;
    text-transform: uppercase;
    transition: color .3s ease;
    a {
      color: #fff;
    }
    &:hover a {
      color: $yellow;
    }
  }
  .header__lang-mk {
    border-right: 1px solid #fff;
  }
  .header__lang-link .active {
    color: $yellow;
  }
  // hamburger
  .icon-bars {
    display: inline-block;
    margin-left: 25px;
    margin-right: 25px;
    font-size: 25px;
    color: #fff;
  }
}

// HERO
.hero {
  position: relative;
  width: 100%;
  min-height: calc(100vh - 110px);
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  .hero__magnifier {
    position: absolute;
    top: 20px;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 0;
    height: 110vh;
    background-image: url("../img/hero-magnifier.png");
    background-position-x: center;
    background-size: contain;
    background-repeat: no-repeat;
    @media only screen and (max-width: 768px) {
      background-image: none;
    }
  }
  .hero__inner {
    padding-bottom: 24px;
    min-height: calc(100vh - 110px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    @media only screen and (max-width: 768px) {
      padding-top: 40px;
      padding-bottom: 60px;
      flex-direction: row;
    }
  }
  .hero__data {
    position: absolute;
    top: 7px;
    right: 6%;
    z-index: 10;
    @media only screen and (max-width: 768px) {
      position: static;
    }
  }
  .hero__poster {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    @media only screen and (max-width: 640px) {
      flex-direction: column;
      align-items: center;
    }
  }
  .hero__poster-inscription {
    width: 432px;
    max-width: 433px;
    min-width: 300px;
    @media only screen and (max-width: 480px) {
      width: 100%;
    }
    img {
      width: 100%;
    }
  }
  .hero__poster-moto {
    width: 36%;
    margin-top: 41px;
    margin-left: 32px;
    font-family: $font-kelly;
    line-height: 1.2;
    letter-spacing: 8px;
    @include rfs(34);
    line-height: 1.2;
    letter-spacing: 8px;
    @media only screen and (max-width: 640px) {
      margin-top: 5px;
    }
    span {
      display: inline-block;
      margin-bottom: 10px;
      padding-left: 5px;
      padding-right: 5px;
      white-space: nowrap;
      background-color: #fff;
      &:last-child {
        margin-left: 25px;
      }
    }
  }
  .hero__nav-wrap {
    width: 100%;
  }
  .hero__nav {
    display: flex;
    margin-top: 115px;
    justify-content: center;
    @media only screen and (max-width: 768px) {
      flex-wrap: wrap;
      margin-top: -100px;
    }
  }
  .hero__nav-item {
    width: 154.13px;
    height: 53.13px;
    margin-left: 27px;
    margin-right: 27px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: $font-kelly;
    letter-spacing: 1.3px;
    color: $yellow;
    @include rfs(20);
    text-transform: uppercase;
    background-image: url("../img/link-bg.svg");
    background-size: cover;
    background-repeat: no-repeat;
    transition: color .2s ease;
    @media only screen and (max-width: 768px) {
      margin-top: 20px;
    }
    &:hover {
      color: #fff;
    }
    &:first-child {
      margin-left: 0;
    }
  }
}

// Footer
.footer {
  flex: 0 0 auto;
  padding-top: 25px;
  padding-bottom: 25px;
  background-color: #000;
  .footer__signup {
    @media only screen and (max-width: 992px) {
      max-width: 640px;
      margin: 0 auto;
    }
  }
  .signup__title {
    font-family: $font-kelly;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 4px;
  }
  .form-group {
    margin-bottom: 5px;
  }
  .signup__label {
    margin-bottom: 5px;
    color: #fff;
    font-size: 10px;
    font-weight: 300;
    span {
      color: $yellow;
    }
  }
  input {
    height: 25px;
    max-width: 393px;
    font-size: 14px;
    @media only screen and (max-width: 992px) {
      max-width: 100%;
    }
  }
  .signup__submit {
    margin-top: 15px;
    font-size: 15px;
    text-transform: uppercase;
    font-family: $font-kelly;
    letter-spacing: 4px;
    background-color: $yellow;
    color: #000;
    border: none;
  }
  .social-iconset {
    margin: 0 auto;
    @media only screen and (max-width: 992px) {
      margin: 40px auto 40px;
    }
  }
  .social-icon {
    min-width: 26px;
    width: 26px;
    height: 26px;
    .icon {
      font-size: 19px;
    }
  }
  .footer__content {
    max-width: 640px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: center;
  }
  .footer__nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .footer__nav,
  .nav-link {
    padding: 0;
    @include rfs(12);
  }
  .footer__description {
    font-size: 11px;
    color: #fff;
  }
  .footer__pictures {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  .footer__picture--logo {
    width: 75px;
    height: 67px;
  }
  .footer__picture--fes {
    width: 76.9px;
    height: 71.71px;
  }
}

//  PROGRAM PAGE
.program {
  padding-top: 52px;
  padding-bottom: 30px;
  &__festname {
    text-align: center;
    img {
      max-width: 314px;
    }
  }
  &__title {
    display: flex;
    margin: 16px auto 60px;
    padding: 10px 20px;
    font-size: 28px;
    letter-spacing: 5px;
    font-family: $font-kelly;
    color: #fff;
    text-transform: uppercase;
    background-color: #000;
  }
  .program__days {}
  .day {
    margin-bottom: 30px;
    // day header
    .day__header {
      margin-bottom: 33px;
      display: flex;
      align-items: center;
      font-family: $font-kelly;
    }
    .day__number {
      position: relative;
      font-size: 69px;
      &:before {
        content: "";
        position: absolute;
        top: 5px;
        left: -16px;
        width: 65px;
        height: 64px;
        transform: rotate(-10deg);
        background-color: $yellow;
        z-index: -1;
      }
    }
    .day__date {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-left: 24px;
      text-transform: uppercase;
    }
    .day__numeric {
      margin-bottom: 8px;
      padding: 3.5px 4px 3.5px 5px;
      text-align: center;
      font-size: 15px;
      letter-spacing: 3px;
      color: #fff;
      background-color: #000;
    }
    .day__month {
      font-size: 22px;
      letter-spacing: 4px;
    }
  }
  // day events
  .day__event-list {
    padding: 0;
  }
  .day__event-item {
    margin-bottom: 10px;
    margin-left: -9px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    font-size: 14px;
    font-weight: 700;
    line-height: 1;
  }
  .day__event-content {
    display: flex;
    margin-left: 31px;
  }
  .day__event-time {}
  .day__event-icon {
    margin-top: 2px;
    position: relative;
    width: 23px;
    &:before {
      content: "";
      position: absolute;
      top: -5px;
      left: 2px;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      background-color: $yellow;
      z-index: -1;
    }
    .icon--music {
      display: inline-block;
      margin-top: -4px;
      width: 22.82px;
      height: 25.77px;
    }
    .icon--film {
      width: 23.16px;
      height: 23.16px;
    }
    .icon--mic {
      display: inline-block;
      margin-top: -4px;
      width: 16px;
      height: 28px;
    }
  }
  .day__event-text {
    margin-left: 8px;
  }
  .day__event-name {
    font-size: 15px;
    font-weight: 700;
    letter-spacing: -0.5px;
  }
  .day__event-desc {
    margin-bottom: 0;
    margin-top: -2px;
    font-size: 12px;
    font-weight: 300;
  }
}

// CONTACT PAGE
.contact {
  padding-top: 50px;
  padding-bottom: 50px;
  .contact__tiles {}
  .contact__tile {
    margin-bottom: 20px;
    img {
      width: 100%;
    }
  }
  .contact__tile-caption {
    display: inline-block;
    margin-bottom: 20px;
    padding: 5px 11px;
    font-family: $font-kelly;
    font-size: 22px;
    color: #fff;
    letter-spacing: 4px;
    text-transform: uppercase;
    background-color: #000;
  }
}

@import "media";